<template>
    <div>
        <div class="card text-center border-0">
            <div class="card-header bg-transparent border-0" v-bind:id="headingName" >
                <h5 class="mb-0">
                    <button 
                        class="btn btn-block collapsed" 
                        type="button" 
                        data-toggle="collapse" 
                        v-bind:data-target="cardCollapsableAnchor" 
                        aria-expanded="false" 
                        v-bind:aria-controls="cardCollapsableName"
                        v-bind:class="setAlert"
                    >
                        {{instrument.instrument}}: <span>{{ close }}</span>
                    </button>
                </h5>
            </div>

            <div 
                v-bind:id="cardCollapsableName" 
                class="collapse container mx-3 w-auto" 
                v-bind:aria-labelledby="headingName" 
                data-parent="#accordionInstrument">
                <!-- higher timeframe Card -->
                <div class="card mb-2 border-0">
                    <div class="card-header border" v-bind:class="highTimeframeClass">{{highTimeframeData.timeframe}} - {{ highTimeframeData.time }}</div>
                    <div class="card-body pb-0 px-0">
                        <table class="table table-bordered table-sm mb-0">
                            <thead>
                                <tr>
                                    <th>Stoch(14)</th>
                                    <th>StochRsi(14)</th>
                                    <th>Stoch(6)</th>
                                    <th>Rsi(14)</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>{{ highTimeframeData.indicatorStochLong.stoch.toPrecision(2) }}</td>
                                    <td>{{ highTimeframeData.stochRsi.stochRsi.toPrecision(2) }}</td>
                                    <td>{{ highTimeframeData.indicatorStochShort.stoch.toPrecision(2) }}</td>
                                    <td>{{ highTimeframeData.rsi.toPrecision(2) }}</td>
                                </tr>
                            </tbody>
                        </table>
                        <table class="table table-bordered table-sm mt-0">
                            <thead>
                                <tr>
                                    <th colspan="3">Bollinger(34)</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>{{ highTimeframeData.indicatorBolBand.lowerBand.toPrecision(5) }}</td>
                                    <td>{{ highTimeframeData.indicatorBolBand.bolBand.toPrecision(5) }}</td>
                                    <td>{{ highTimeframeData.indicatorBolBand.upperBand.toPrecision(5) }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <!-- End higher timeframe Card -->

                <!-- mid timeframe Card -->
                <div class="card mb-2 border-0">
                    <div class="card-header border" v-bind:class="midTimeframeClass">{{midTimeframeData.timeframe}} - {{ midTimeframeData.time }}</div>
                    <div class="card-body pb-0 px-0">
                        <table class="table table-bordered table-sm mb-0">
                            <thead>
                                <tr>
                                    <th colspan="2">Stoch(14)</th>
                                    <th colspan="2">StochRsi(14)</th>
                                    <th colspan="2">Stoch(6)</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>{{ midTimeframeData.indicatorStochLong.stoch.toPrecision(2) }}</td>
                                    <td>{{ midTimeframeData.indicatorStochLong.smoothedStoch.toPrecision(2) }}</td>
                                    <td>{{ midTimeframeData.stochRsi.stochRsi.toPrecision(2) }}</td>
                                    <td>{{ midTimeframeData.stochRsi.smoothedStochRsi.toPrecision(2) }}</td>
                                    <td>{{ midTimeframeData.indicatorStochShort.stoch.toPrecision(2) }}</td>
                                    <td>{{ midTimeframeData.indicatorStochShort.smoothedStoch.toPrecision(2) }}</td>
                                </tr>
                            </tbody>
                        </table>
                        <table class="table table-bordered table-sm mt-0">
                            <thead>
                                <tr>
                                    <th colspan="3">Bollinger(34)</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>{{ midTimeframeData.indicatorBolBand.lowerBand.toPrecision(5) }}</td>
                                    <td>{{ midTimeframeData.indicatorBolBand.bolBand.toPrecision(5) }}</td>
                                    <td>{{ midTimeframeData.indicatorBolBand.upperBand.toPrecision(5) }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <!-- End mid timeframe Card -->

                <!-- low timeframe Card -->
                <div class="card border-0">
                    <div class="card-header border" v-bind:class="lowTimeframeClass">{{lowTimeframeData.timeframe}} - {{ lowTimeframeData.time }}</div>
                    <div class="card-body pb-0 px-0">
                        <table class="table table-bordered table-sm mb-0">
                            <thead>
                                <tr>
                                    <th>Sma(25)</th>
                                    <th>Ema(18)</th>
                                    <th>Wma(7)</th>
                                    <th>AwOsc(4,35)</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>{{ lowTimeframeData.indicatorSma.toPrecision(5) }}</td>
                                    <td>{{ lowTimeframeData.indicatorEma.toPrecision(5) }}</td>
                                    <td>{{ lowTimeframeData.indicatorWma.toPrecision(5) }}</td>
                                    <td>{{ lowTimeframeData.indicatorAwesomeOsc.toPrecision(5) }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <!-- End low timeframe Card -->
            </div>
        </div>
    </div>
</template>

<script>
import _ from 'lodash'
export default {
    name: "InstrumentComponent",
    props: {
        instrument: Object
    },
    data(){
        return {
            cardCollapsableName: 'cardCollasable'+this.instrument.instrument,
            cardCollapsableAnchor: '#cardCollasable'+this.instrument.instrument,
            headingName: 'heading-'+this.instrument.instrument,
        }
    }, 
    computed: {
        highTimeframeData(){
            return _.filter(this.instrument.data, item => {
                if(item.flag == 'high') return item;
            })[0]
        },
        midTimeframeData(){
            return _.filter(this.instrument.data, item => {
                if(item.flag == 'mid') return item;
            })[0]
        },
        lowTimeframeData(){
            return _.filter(this.instrument.data, item => {
                if(item.flag == 'low') return item;
            })[0]
        },
        close(){
            return this.lowTimeframeData.close
        },
        open(){
            return this.lowTimeframeData.open
        },
        highTimeframeClass(){
            return {
                'bg-danger text-white': this.highTimeframeData.signal4High == 'SELL',
                'bg-success text-white': this.highTimeframeData.signal4High == 'BUY'
            }
        },
        midTimeframeClass(){
            return {
                'bg-danger text-white': this.midTimeframeData.signal4Mid == 'SELL',
                'bg-success text-white': this.midTimeframeData.signal4Mid == 'BUY'
            }
        },
        lowTimeframeClass(){
            return {
                'bg-danger text-white': this.lowTimeframeData.signal4Low == 'SELL',
                'bg-success text-white': this.lowTimeframeData.signal4Low == 'BUY',           
            }
        },
        setAlert(){
            return {
                'btn-danger': this.highTimeframeData.signal4High == 'SELL' && 
                              this.midTimeframeData.signal4Mid == 'SELL' &&
                              this.LowTimeframeData.signal4Low == 'SELL',
                'btn-success': this.highTimeframeData.signal4High == 'BUY' && 
                              this.midTimeframeData.signal4Mid == 'BUY' &&
                              this.LowTimeframeData.signal4Low == 'BUY',
            }
        }
    }
}
</script>

