<template>
  <div class="container pt-2">
    <div class="card mt-5">
      <div class="card-body">
        <div class="alert alert-danger alert-dismissible fade show" role="alert" v-show="isError">
          <span>{{messageError}}</span>
          <button type="button" class="close" data-dismiss="alert" aria-label="Close" v-on:click="isError=false">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <form v-on:submit.prevent="login">
          <div class="form-group">
            <label for="email">Correo Electrónico</label>
            <input 
              type="email" 
              class="form-control" 
              id="email" 
              aria-describedby="emailHelp" 
              placeholder="Correo electrónico"
              v-model="email"
              v-bind:class="{ 'is-invalid': isError && email=='' }">
              <div class="invalid-feedback">Debe ingresar su email</div>
          </div>
          <div class="form-group">
            <label for="password">Clave</label>
            <input 
              type="password" 
              class="form-control" 
              id="password" 
              placeholder="Ingrese su clave de acceso"
              v-model="password"
              v-bind:class="{ 'is-invalid': isError && password=='' }">
              <div class="invalid-feedback">Debe ingresar la clave de acceso</div>
          </div>
          <button 
            type="submit" 
            class="btn btn-primary">
            Ingresar
          </button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'login',
  data(){
    return {
      email: '',
      password: '',
      isError: false,
      messageError: ''
    }
  },
  methods: {
    login(){
      axios.post('/users/login', {
        email: this.email,
        password: this.password
      }).then(resp => {
        if (resp.status === 200) {
          if(resp.data.isActive){
            const xAuth = {
              token: resp.headers['x-auth']
            }
            window.localStorage.setItem('binarySignalToken', JSON.stringify(xAuth));
            this.$parent.$parent.$emit('login');
          } else {
            this.isError=true;
            this.messageError="Esta cuenta no esta activa, contactanos."
          }
        }
      }).catch(() => {
        this.password = '';
        this.isError = true;
        this.messageError="El email y password no coinciden."
      })
    }
  }

}
</script>

<style lang="scss" scoped>
  .container {
    height: 100vh;
  }
</style>