import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'
import socketio from 'socket.io-client'
import VueSocketIO from 'vue-socket.io'
import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'

export const SocketInstance = socketio('https://shrouded-woodland-53778.herokuapp.com/');
Vue.use(VueSocketIO, SocketInstance)

Vue.config.productionTip = false

new Vue({
  router,
  data(){
    return {
      isMaster: false,
      isLogged: false,
      token: null
    }
  },
  render: h => h(App),
  created () {
    this.token = JSON.parse(window.localStorage.getItem('binarySignalToken'))
    if (this.token) {
      this.checkToken(this.token);
    }
    this.$on('login', () => {
      this.token = JSON.parse(window.localStorage.getItem('binarySignalToken'))
      this.checkToken(this.token);
      this.$router.push('/dashboard');
    })
    this.$on('isMaster', () => {
      this.isMaster = true
    })
    this.$on('logout', () => {
      window.localStorage.removeItem('binarySignalToken')
      this.token = null
      this.checkToken(this.token)
      this.$router.push('/')
    })
  },
  methods: {
    checkToken(token){
      if(token){
        axios.post('/users/me', {}, {
          headers: {
            'Content-Type': 'application/json',
            'X-Auth': token.token
          }
        }).then(resp => {
          this.isMaster = resp.data.user.isMaster
          this.isLogged = true
        }).catch(() => {
          this.isMaster = false
          this.isLogged = false
          window.localStorage.removeItem('binarySignalToken')
        });
      } else {
        this.isLogged = false
        this.isMaster = false
      }
    }
  }
}).$mount('#app')
