<template>
  <div class="container">
    <button class="btn btn-success btn-block mt-5" v-on:click="openNewUserForm">Agregar Nuevo Usuario</button>
    <table class="table text-center mt-4 table-sm">
        <thead>
          <tr>
            <th class="d-none d-sm-block">Usuario</th>
            <th>Correo</th>
            <th>Activo</th>
            <th class="d-none d-sm-block">Acción</th>
          </tr>
        </thead>
        <tbody>
          <user-component v-for="user in users" v-bind:key="user._id" v-bind:user="user" v-on:updateAcitveField="updateAcitveField" v-on:deleteField="deleteField"/>
        </tbody>
    </table>

    <div class="modal fade" id="dialogModal" tabindex="-1" role="dialog" aria-labelledby="dialogModalTitle" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="dialogModalTitle">Atención</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <p>{{ msg }}</p>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-dismiss="modal" v-on:click="cancelAction">Cancelar</button>
                    <button type="button" class="btn btn-primary" v-on:click="doAction">Continuar</button>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import _ from 'lodash'
import $ from 'jquery'
import UserComponent from './../components/UserComponent'

export default {
  name: 'users',
  components: {
    'user-component': UserComponent,
  },
  data(){
    return {
      users: [],
      userObjective: {},
      action: '',
      msg: ''
    }
  },
  created(){
    this.getAllUsers()
  },
  computed: {
    
  },
  methods: {
    getAllUsers () {
      this.users = []
      this.token = JSON.parse(window.localStorage.getItem('binarySignalToken'))
      axios.post('/users/all', {}, {
        headers: {
          'Content-Type': 'application/json',
          'X-Auth': this.token.token
        }
      })
        .then(resp => {
          if (resp.status === 200) {
            this.users = _.filter(resp.data, o => { return !o.isMaster })
          }
        }).catch(() => {})
    },
    openNewUserForm(){
      this.$router.push('/new_user');
    },
    updateAcitveField(user, msg){
      this.userObjective = user
      this.action = 'updateActiveField'
      this.msg = msg
      $('#dialogModal').modal('show')
    },
    deleteField(user, msg){
      this.userObjective = user
      this.action = 'deleteField'
      this.msg = msg
      $('#dialogModal').modal('show')
    },
    cancelAction(){
      this.action = ''
      this.msg = ''
      this.userObjective = {}
    },
    doAction(){
      const token = JSON.parse(window.localStorage.getItem('binarySignalToken'))
      if(this.action == 'updateActiveField'){
        axios.post(`/users/${this.userObjective._id}/update`, {
            user: {isActive: !this.userObjective.isActive},
        },{
            headers: {
                'Content-Type': 'application/json',
                'X-Auth': token.token
            }
        }).then(resp => {
            if(resp){
              this.getAllUsers()
              this.action = ''
              this.msg = ''
              this.userObjective = {}
              $('#dialogModal').modal('hide')
            }
        }).catch(() => {
            alert(`No es posible modificar el registro ${this.userObjective.username}`)
        })
      } else if(this.action == 'deleteField') {
        axios.post(`/users/${this.userObjective._id}/delete`, {
            user: {isActive: !this.userObjective.isActive},
        },{
            headers: {
                'Content-Type': 'application/json',
                'X-Auth': token.token
            }
        }).then(resp => {
            if(resp){
              this.getAllUsers()
              this.action = ''
              this.msg = ''
              this.userObjective = {}
              $('#dialogModal').modal('hide')
            }
        }).catch(() => {
            alert(`No es posible eliminar el registro ${this.userObjective.username}`)
        })
      }
    },

  },
}
</script>

