<template>
    <div class="container mt-4">
        <div class="card">
            <div class="card-body">
                <div class="alert alert-danger alert-dismissible fade show" role="alert" v-if="isError">
                    <ul v-for="message in messageError" v-bind:key="message">
                        <li>{{ message }}</li>
                    </ul>
                    <button type="button" class="close" data-dismiss="alert" v-on:click="isError=false">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <form v-on:submit.prevent="registerNewUser">
                    <div class="form-group">
                        <label for="firstName">Nombres</label>
                        <input 
                            type="text" 
                            class="form-control" 
                            id="firstName" 
                            aria-describedby="firstNameHelp" 
                            v-model="firstName">
                    </div>
                    <div class="form-group">
                        <label for="lastName">Apellidos</label>
                        <input 
                            type="text" 
                            class="form-control" 
                            id="lastName" 
                            aria-describedby="lastNameHelp" 
                            v-model="lastName">
                    </div>
                    <div class="form-group">
                        <label for="username">Nombre de Usuario *</label>
                        <input 
                            type="text" 
                            class="form-control" 
                            id="username" 
                            aria-describedby="usernameHelp" 
                            v-model="username"
                            v-bind:class="{ 'is-invalid': isError && username=='' }">
                        <div class="invalid-feedback">Debe ingresar su nombre de usuario</div>
                    </div>
                    <div class="form-group">
                        <label for="email">Correo Electrónico *</label>
                        <input 
                            type="email" 
                            class="form-control" 
                            id="email" 
                            aria-describedby="emailHelp" 
                            v-model="email"
                            v-bind:class="{ 'is-invalid': isError && email=='' }">
                        <div class="invalid-feedback">Debe ingresar su email</div>
                    </div>
                    <div class="form-group">
                        <label for="phone">Teléfono de contácto</label>
                        <input 
                            type="text" 
                            class="form-control" 
                            id="phone" 
                            aria-describedby="phoneHelp" 
                            v-model="phone">
                    </div>
                    <div class="form-group">
                        <label for="password">Clave *</label>
                        <input 
                            type="password" 
                            class="form-control" 
                            id="password" 
                            v-model="password"
                            v-bind:class="{ 'is-invalid': isError && password=='' }">
                        <div class="invalid-feedback">Debe ingresar la clave de acceso</div>
                    </div>
                    <div class="form-group">
                        <label for="repassword">Confirmar contraseña *</label>
                        <input 
                            type="password" 
                            class="form-control" 
                            id="repassword" 
                            v-model="repassword"
                            v-bind:class="{ 'is-invalid': isError && repassword=='' }">
                        <div class="invalid-feedback">Debe ingresar la clave de acceso</div>
                    </div>
                    <button type="submit" class="btn btn-success btn-block">Crear</button>
                    <button type="button" class="btn btn-danger btn-block" v-on:click="closeWindow">Cancelar</button>
                </form>
            </div>
        </div>
    </div>
</template>



<script>
import axios from 'axios'

export default {
    name: 'new_user',
    data(){
        return {
            firstName: '',
            lastName: '',
            username: '',
            email: '',
            phone: '',
            password: '',
            repassword: '',
            isError: false,
            messageError: []
        }
    },
    methods: {
        checkFields(){
            this.isError = false;
            this.messageError = []
            if(this.username == ''){
                this.isError = true;
                this.messageError.push('El campo nombre de usuario no debe esta en blanco')
            }
            if(this.email == ''){
                this.isError = true;
                this.messageError.push('El campo email no debe estar en blanco')
            }
            if(this.password == ''){
                this.isError = true;
                this.messageError.push('El campo contraseña no debe estar en blanco')
            }
            if(this.password != this.repassword){
                this.isError = true;
                this.messageError.push('Las contraseñas no coinciden')
            }
        },
        registerNewUser(){
            this.checkFields()
            if(!this.isError){
                const token = JSON.parse(window.localStorage.getItem('binarySignalToken'))
                axios.post('/users/register', {
                    firstName: this.firstName,
                    lastName: this.lastName,
                    username: this.username,
                    email: this.email,
                    phone: this.phone,
                    password: this.password
                },{
                    headers: {
                        'Content-Type': 'application/json',
                        'X-Auth': token.token
                    }
                }).then(resp => {
                    if(resp.status == 200){
                        alert('Usuario creado exitosamente')
                        this.$router.push('/users');
                    }
                }).catch((e) => {
                    this.isError = true;
                    this.messageError.push('No es posible crear un nuevo usuario, revise sus datos')
                    console.log(e)
                })
            }
        },
        closeWindow(){
            this.$router.push('/users')
        }
    }

}
</script>


<style>

</style>
