<template>
    <div class="container mt-4">
        <div class="card">
            <div class="card-body">
                <div class="alert alert-danger alert-dismissible fade show" role="alert" v-if="isError">
                    <ul v-for="message in messageError" v-bind:key="message">
                        <li>{{ message }}</li>
                    </ul>
                    <button type="button" class="close" data-dismiss="alert" v-on:click="isError=false">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <form v-on:submit.prevent="updateUser">
                    <div class="form-group">
                        <label for="firstName">Nombres</label>
                        <input 
                            type="text" 
                            class="form-control" 
                            id="firstName" 
                            aria-describedby="firstNameHelp" 
                            v-model="user.firstName">
                    </div>
                    <div class="form-group">
                        <label for="lastName">Apellidos</label>
                        <input 
                            type="text" 
                            class="form-control" 
                            id="lastName" 
                            aria-describedby="lastNameHelp" 
                            v-model="user.lastName">
                    </div>
                    <div class="form-group">
                        <label for="username">Nombre de Usuario *</label>
                        <input 
                            type="text" 
                            class="form-control" 
                            id="username" 
                            aria-describedby="usernameHelp" 
                            v-model="user.username"
                            v-bind:class="{ 'is-invalid': isError && username=='' }">
                        <div class="invalid-feedback">Debe ingresar su nombre de usuario</div>
                    </div>
                    <div class="form-group">
                        <label for="email">Correo Electrónico *</label>
                        <input 
                            type="email" 
                            class="form-control" 
                            id="email" 
                            aria-describedby="emailHelp" 
                            v-model="user.email"
                            v-bind:class="{ 'is-invalid': isError && email=='' }">
                        <div class="invalid-feedback">Debe ingresar su email</div>
                    </div>
                    <div class="form-group">
                        <label for="phone">Teléfono de contácto</label>
                        <input 
                            type="text" 
                            class="form-control" 
                            id="phone" 
                            aria-describedby="phoneHelp" 
                            v-model="user.phone">
                    </div>
                    <button type="submit" class="btn btn-success btn-block">Actualizar</button>
                    <button type="button" class="btn btn-danger btn-block" v-on:click="closeEdit">Cancelar</button>
                </form>
            </div>
        </div>
    </div>
</template>



<script>
import axios from 'axios'

export default {
    name: 'edit_user',
    data(){
        return {
            id:'',
            user: {},
            isError: false,
            messageError: []
        }
    },
    created(){
        this.id = this.$route.params.id
        this.loadUserData()
    },
    methods: {
        checkFields(){
            this.isError = false;
            this.messageError = []
            if(this.user.username == ''){
                this.isError = true;
                this.messageError.push('El campo nombre de usuario no debe esta en blanco')
            }
            if(this.user.email == ''){
                this.isError = true;
                this.messageError.push('El campo email no debe estar en blanco')
            }
        },
        loadUserData(){
            const token = JSON.parse(window.localStorage.getItem('binarySignalToken'))
            axios.post(`/users/${this.id}`, {}, {
                    headers: {
                        'Content-Type': 'application/json',
                        'X-Auth': token.token
                    }
                }).then(resp => {
                    if(resp.status == 200){
                        this.user = resp.data
                    }
                }).catch((e) => {
                    this.isError = true;
                    this.messageError.push('No es posible crear un nuevo usuario, revise sus datos')
                    console.log(e)
                })
        },
        updateUser(){
            this.checkFields()
            if(!this.isError){
                const token = JSON.parse(window.localStorage.getItem('binarySignalToken'))
                axios.post(`/users/${this.id}/update`, {
                    user: this.user
                },{
                    headers: {
                        'Content-Type': 'application/json',
                        'X-Auth': token.token
                    }
                }).then(resp => {
                    alert(`Se actualizo el registro ${resp.data._id} exitosamente`)
                    this.$router.push('/users')
                }).catch(() => {
                    alert(`No es posible eliminar el registro ${this.userObjective.username}`)
                })
            }
        },
        closeEdit(){
            this.$router.go(-1)
        }
    }

}
</script>


