<template>
    <div class="container my-4">
        <div class="card my-4 border-0">
            <div class="card-header text-center bg-dark text-white">
                <strong>PANEL</strong>
                <div class="float-right">
                    <button class="btn btn-sm" v-bind:class="{ 'btn-primary': isAudioAlarm, 'btn-secondary' : !isAudioAlarm }" v-on:click="audioToggle">Audio {{ isAudioAlarm ? 'On' : 'Off' }}</button>
                </div>
            </div>
            
            <div class="my-2 mx-4">
                <table class="table table-sm text-center" v-show="checkActionList">
                    <thead>
                        <th colspan="2">Lista de Alertas</th>
                        <th colspan="2">Lista de Acciones</th>
                    </thead>
                    <tbody>
                        <tr>
                            <td>
                                <div 
                                    v-for="alert in buyAlertList" 
                                    v-bind:key="alert.instrument">
                                    <span class="badge badge-success"> {{ alert.instrument }}</span>
                                </div>
                            </td>
                            <td>
                                <div 
                                    v-for="alert in sellAlertList" 
                                    v-bind:key="alert.instrument">
                                    <span class="badge badge-danger"> {{ alert.instrument }}</span>
                                </div>
                            </td>
                            <td>
                                <div 
                                    v-for="action in buyActionList" 
                                    v-bind:key="action.instrument">
                                    <span class="badge badge-success"> {{ action.instrument }}</span>
                                </div>
                            </td>
                            <td>
                                <div 
                                    v-for="action in sellActionList" 
                                    v-bind:key="action.instrument">
                                    <span class="badge badge-danger"> {{ action.instrument }}</span>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <div class="accordion" id="accordionInstrument">
            <instrument-component 
                v-for="instrument in instruments" 
                v-bind:key="instrument.instrument" 
                v-bind:instrument="instrument"
                />
        </div>
    </div>    
</template>

<script>
import InstrumentComponent from './../components/InstrumentComponent'
import _ from 'lodash'

export default {
    name: 'dashboard',
    components: {
        'instrument-component': InstrumentComponent,
    },
    data(){
        return {
            instruments: [],
            symbols: [],
            timeframes: [],
            flags:[],
            isAudioAlarm: true,
        }
    },
    methods: {
        playSound (sound) {
            if(sound) {
                var audio = new Audio(sound);
                audio.play();
            }
        },
        
        audioToggle(){
            this.isAudioAlarm = !this.isAudioAlarm
        }
    },
    computed: {
        sellAlertList(){
            return _.filter(this.instruments, item => {
                const subItem = _.filter(item.data, subItem => {
                    if(subItem.flag == 'high') return subItem;
                })
                return (subItem.signal4High == 'SELL')
            })
        }, 
        buyAlertList(){
            return _.filter(this.instruments, item => {
                const subItem = _.filter(item.data, subItem => {
                    if(subItem.flag == 'high') return subItem;
                })
                return (subItem.signal4Hig == 'BUY')
            })
        }, 
        sellActionList(){
            return _.filter(this.instruments, item => {
                const subItemHigh = _.filter(item.data, subItem => {
                    if(subItem.flag == 'high') return subItem;
                })
                const subItemMid = _.filter(item.data, subItem => {
                    if(subItem.flag == 'mid') return subItem;
                })
                const subItemLow = _.filter(item.data, subItem => {
                    if(subItem.flag == 'low') return subItem;
                })
                return (subItemHigh.signal4High == 'SELL'  && subItemMid.signal4Mid == 'SELL' && subItemLow.signal4Low == 'SELL')
            })
        },
        buyActionList(){
            return _.filter(this.instruments, item => {
                const subItemHigh = _.filter(item.data, subItem => {
                    if(subItem.flag == 'high') return subItem;
                })
                const subItemMid = _.filter(item.data, subItem => {
                    if(subItem.flag == 'mid') return subItem;
                })
                const subItemLow = _.filter(item.data, subItem => {
                    if(subItem.flag == 'low') return subItem;
                })
                return (subItemHigh.signal4High == 'BUY'  && subItemMid.signal4Mid == 'BUY' && subItemLow.signal4Low == 'BUY')
            })
        },
        checkActionList(){
            if(this.isAudioAlarm) {
                if((Array.isArray(this.sellActionList) && this.sellActionList.length === 0) || (Array.isArray(this.buyActionList) && this.buyActionList.length === 0)){
                    return false
                } else {
                    this.playSound('http://soundbible.com/mp3/Elevator Ding-SoundBible.com-685385892.mp3')
                    return true
                }
            }
        },
    },
    sockets:{
        connect(){
            this.connected = true
        },
        disconnect(){
            this.connected = false
        },
        fxData(fxdata){
            this.symbols = _.uniq(_.map(fxdata, item => item.instrument))
            this.timeframes =  _.uniq(_.map(fxdata, item => item.timeframe))
            this.flags =  _.uniq(_.map(fxdata, item => item.flag))
            this.instruments = []

            _.forEach(this.symbols, symbol => {
                let arr = []
                _.forEach(this.flags, flag => {
                    arr.push(_.filter(fxdata, {'instrument': symbol, 'flag': flag})[0])
                })
                const instrument = {
                    instrument: symbol,
                    data: arr
                }
                this.instruments.push(instrument)
            })
        }
    }
}
</script>

