<template>
    <tr>
        <td class="d-none d-sm-block">{{ user.username }}</td>
        <td>{{ user.email }}</td>
        <td>
            <button class="btn btn-sm" v-bind:class="{ 'btn-success': user.isActive, 'btn-danger': !user.isActive }" v-on:click="updateActiveField">
                <i class="fa fa-check" aria-hidden="true" v-show="user.isActive"></i>
                <i class="fa fa-times" aria-hidden="true" v-show="!user.isActive"></i>
            </button>
        </td>
        <td class="d-none d-sm-block">
            <button class="btn btn-danger btn-sm" v-on:click="deleteField"><i class="fa fa-trash"></i></button>
            <span class="mx-1"></span>
            <button class="btn btn-warning btn-sm" v-on:click="editUser"><i class="fa fa-pencil"></i></button>
        </td>
    </tr>
</template>

<script>

export default {
    name: 'userComponent',
    props: {
        user: Object
    },
    data(){
        return {

        }
    },
    methods: {
        updateActiveField(){
            this.$emit('updateAcitveField', this.user, `Desea cambiar el estado de el usuario ${this.user.username}`)
        },
        deleteField(){
            this.$emit('deleteField', this.user, `Desea cambiar eliminar el usuario ${this.user.username} permanentemente`)
        },
        editUser(){
            this.$router.push({name:'edit_user', params:{id: this.user._id}})
        }
    },
    computed: {
        
    }
}
</script>
